import React from "react";
import { Box, Container } from "@material-ui/core";
import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import HomeIcon from '@material-ui/icons/Home';
import VideogameAssetIcon from "@material-ui/icons/VideogameAsset";
import EmojiEventsIcon from "@material-ui/icons/EmojiEvents";
import OpacityIcon from "@material-ui/icons/Opacity";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { colors } from "../theme/colors.theme";

const Experience = () => {
  const { black, white, blackLight } = colors;
  const experiences = [
    {
      time: "2018 - Actualidad",
      title: "Chief Technology Officer @ Torneos.GG",
      description: `Encargado del área de tecnología. También soy el encargado del frontend.`,
      image: "./assets/images/tgg.png",
      icon: <EmojiEventsIcon />,
      color: "secondary",
    },
    {
      time: "2019 - 2020",
      title: "Transformación digital en una ASADA",
      description: `Agilizar los procesos administrativos de la ASADA mediante el desarrollo de una
      plataforma web que facilite la gestión y organización en la
      asociación.
      Encargado tanto del frontend como del backend.`,
      image: "./assets/images/asadasop.png",
      icon: <OpacityIcon />,
      color: "primary",
      variant: "outlined",
    },
    {
      time: "2020",
      title: "Desarrollo de sistema para reservar habitaciones",
      description: `Realicé algunos sistemas relacionados con la reservación de las habitaciones y también en el sistema administrativo (control de las habitaciones, reservaciones, etc).
      Trabajé como Full Stack.`,
      image: "./assets/images/pangea.png",
      icon: <HomeIcon />,
      color: "secondary",
      variant: "outlined",
    },
    {
      time: "2020 - Actualidad",
      title: "Frontend Developer @ Gamanza",
      description: `Realización de diferentes componentes de UI y vistas para el manejo de la información en un sistema CRM.`,
      image: "./assets/images/gamanza.png",
      icon: <VideogameAssetIcon />,
      color: "primary",
    },
  ];
  return (
    <Box component="section" p={3} bgcolor={black} color={white}>
      <Container>
        <h1 className="section-skills-title">Experiencias</h1>
        <Timeline align="alternate">
          {experiences.map((exp) => (
            <TimelineItem key={exp.title}>
              <TimelineOppositeContent>
                <Box color={white}>
                  <Typography variant="body2">{exp.time}</Typography>
                </Box>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot color={exp.color} variant={exp.variant}>
                  {exp.icon}
                </TimelineDot>
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <Box component={Paper} bgcolor={blackLight} color={white} p={1}>
                  <Typography
                    variant="h6"
                    component="h1"
                    style={{ fontWeight: 700 }}
                  >
                    {exp.title}
                  </Typography>
                  <Typography>{exp.description}</Typography>
                </Box>
                <Box textAlign="right" mt={1} mb={1}>
                  <img src={exp.image} style={{ width: 150 }} alt={exp.title} />
                </Box>
              </TimelineContent>
            </TimelineItem>
          ))}
        </Timeline>
      </Container>
    </Box>
  );
};

export default Experience;
