import React from "react";
import { Grid, Typography, Box } from "@material-ui/core";
import { colors } from "../theme/colors.theme";

const About = () => {
  const { white } = colors;

  return (
    <Box component="section" p={3} bgcolor='#000a4e' color={white}>
      <Grid container spacing={3} alignItems="center">
        <Grid item xs={12} md={6} lg={6}>
          <Box textAlign="center">
            <Typography variant="h2" style={{ fontWeight: 900, color: white }}>
              ¿Quién soy?
            </Typography>
          </Box>
        </Grid>
        <Grid item xs={12} md={6} lg={6}>
          <Box mb={1}>
          <Typography component="p" variant="h5" style={{fontWeight:600}}>
            Soy <span className="about-bold">Fernando Briceño</span>, un desarollador de
            software que vive en <span className="about-bold">Costa Rica</span>,
            actualmente estoy estudiando ingeniería en sistemas en la{" "}
            <a href="https://www.una.ac.cr/" target="_blank" rel="noopener noreferrer"><span style={{textDecoration: 'underline', cursor: 'pointer'}} className="about-bold">UNA</span></a> y me estoy formando
            profesionalmente en el ámbito de programación en{" "}
            <a href="https://platzi.com/" target="_blank" rel="noopener noreferrer"><span className="about-bold">Platzi</span></a>.
          </Typography>
          </Box>
          <Typography component="p" variant="h5" style={{fontWeight:600}}>
            Tengo 23 años, cuento con experiencia en tecnologías tanto de{" "}
            <span className="about-bold">frontend</span> como de{" "}
            <span className="about-bold">backend</span>.
          </Typography>
        </Grid>
      </Grid>
      <Box height={130} />
    </Box>
  );
};

export default About;
